@import url(https://fonts.googleapis.com/css2?family=Otomanopee+One&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:600|Work+Sans:400,500,600,700&display=swap);
main{
  margin: 20px;
  min-height: 80vh;
}

body{
  background: #C0C0C0;
  background: linear-gradient(135deg, #C0C0C0 0%,     #2F4F4F 100%);
  font-family: "work sans";
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.footer__style {
    background-color: #777;
    padding: 10px;
    text-align: center;
    color: white;
}
.navbar__font {
    font-family: 'Otomanopee One', sans-serif !important;
}

.app_title {
    margin-left: 20px;
}

.header__avatar{
    margin-left: auto !important;
}

.header__signin{
    margin-left: auto !important;
}

.header__search {
    margin: auto !important;
    background-color: #1976d2;
    display: flex;
    flex-direction: row;
    height: 40px;
    width: 40vh;
    border-radius: 5px;
    
}

.header__search_icon {
    padding-top: 10px !important;
    padding-left: 20px !important;
    padding-right: 10px !important;
}

.header__search_input {
    border: none;
    background-color: #1976d2;
    color: white;
    
}

.header__search_input:focus{
    outline: none;
    place-content: "";
}

.header__search_input::-webkit-input-placeholder{
    color: #f0f0f0;
}

.header__search_input:-ms-input-placeholder{
    color: #f0f0f0;
}

.header__search_input::placeholder{
    color: #f0f0f0;
}
html{
  font-size: 100%;
}

#loginform{
  max-width: 500px;
  min-width: 300px;
  max-height: 700px;
  width: 30%;
  height: 60%;
  margin: 100px auto;
  background-color: #FFFFFF;
  border-radius: 25px;
}

#headerTitle{
  text-align: center;
  font-family: 'open sans', sans-serif;
  padding: 2rem 0;
  margin: 0;
  font-size: 2rem;
}

.row{
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 2rem;
  max-width: 100%;
  
}

.row input{
  width: 80%;
  box-sizing: border-box;
  border: none;
  font-size: 1.3rem;
  padding-left: 1.5rem;
  padding-bottom: 1rem;
  box-shadow: inset 0px -3px 0px 0px rgba(187,187,187,0.2);
  transition: box-shadow 0.2s ease-in;
}

.row input:focus{
   box-shadow: inset 0px -3px 0px 0px rgba(34,193,195,0.7);
   outline: none;
}

.row input::-webkit-input-placeholder{
  opacity: 1;
  -webkit-transition: opacity 0.25s ease-out;
  transition: opacity 0.25s ease-out;
}

.row input:hover::-webkit-input-placeholder,
.row input:focus::-webkit-input-placeholder{
  opacity: 0;
}

.row label{
  align-self: start;
  padding-left: 4.5rem;
  padding-bottom: 0.5rem;
  color: rgba(187,187,187,0.9);
}

.row button{
  border-radius: 25px;
  width: 80%;
  height: 40px;
  font-size: 1.3rem;
  color: white;
  font-weight: 700;
  background: rgb(34,193,195);
  background: linear-gradient(90deg, rgba(34,193,195,1) 0%,     rgba(253,187,45,1) 100%);
  border: 0px;
  cursor: pointer;
  transition: opacity 0.25s ease-out;
}

.row button:hover{
  opacity: 0.8;
}

#button{
  padding-bottom: 1.5rem;
}

#iconGroup{
  display: flex;
  justify-content: center;
  padding: 2rem 0;
}

#iconGroup a{
  margin: 0 0.6rem;
}

.repos__list {
    border : 5px solid #778899  !important;
    max-width: 80%;
    margin: 20px !important;
}

.repos__icon {
    font-weight: bold !important;
    -webkit-font-feature-settings:"subs" off, "sups" off !important;
            font-feature-settings:"subs" off, "sups" off !important;
    font-variant-position:normal !important;
    border: none;
    border-radius: 10px;
    background-color: darkgrey;
    padding: 10px;
}

.repos__link {
    background-color: #1976d2;
    display: flex;
    flex-direction: row;
    height: 40px;
    width: 60vh;
    border-radius: 5px;
    
}

.repos__link_icon {
    padding-top: 10px !important;
    padding-left: 20px !important;
    padding-right: 10px !important;
}

.repos__link_input {
    border: none;
    width: 100vh;
    background-color: #1976d2;
    color: white;
    
}

.repos__link_input:focus{
    outline: none;
    place-content: "";
}

.repos__link_input::-webkit-input-placeholder{
    color: #f0f0f0;
}

.repos__link_input:-ms-input-placeholder{
    color: #f0f0f0;
}

.repos__link_input::placeholder{
    color: #f0f0f0;
}

