.repos__list {
    border : 5px solid #778899  !important;
    max-width: 80%;
    margin: 20px !important;
}

.repos__icon {
    font-weight: bold !important;
    font-variant-position:normal !important;
    border: none;
    border-radius: 10px;
    background-color: darkgrey;
    padding: 10px;
}

.repos__link {
    background-color: #1976d2;
    display: flex;
    flex-direction: row;
    height: 40px;
    width: 60vh;
    border-radius: 5px;
    
}

.repos__link_icon {
    padding-top: 10px !important;
    padding-left: 20px !important;
    padding-right: 10px !important;
}

.repos__link_input {
    border: none;
    width: 100vh;
    background-color: #1976d2;
    color: white;
    
}

.repos__link_input:focus{
    outline: none;
    place-content: "";
}

.repos__link_input::placeholder{
    color: #f0f0f0;
}
